<script>
   /*************************************************************/
   /* AppContainer for target test                              */
   /* -------------------------                                 */
   /* app container for applying DD-SIMCA model to target class */
   /*************************************************************/

   import { untrack } from 'svelte';
   import { downloadResults, downloadModel, predict } from '../utils';
   import { Container, Button } from 'svelte-controls-basic';
   import { replacer } from '../../../shared/utils.js';

   import DataLoader from '../../../shared/components/PCADataLoader.svelte';
   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import Contributions from '../../../shared/components/Contributions.svelte';

   import ModelLoader from '../shared/ModelLoader.svelte';
   import ModelTable from '../shared/ModelTable.svelte';
   import SIMCASettings from '../shared/SIMCASettings.svelte';

   import ResultTable from './ResultTable.svelte';
   import AppPlots from './AppPlots.svelte';

   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['target']);

   // properties for showing contributions
   let selected = $state(-1);
   let contributionIsShown = $state(false);


   // callback function to set object with a give position in plot series
   // as outlier
   function markOutlier(pos) {

      // check that position is valid
      if (pos < 0 || pos > s.testres.pca.nrows - 1) return;

      // find sample ID corresponding to the position
      // this is needed if some of the objects already
      // removed from results as outliers
      const id = s.testres.pca.objindices[pos];

      // add the object to outliers array and reset the model and all related outcomes
      if (id >= 0 && !s.outliers.includes(id)) {
         s.outliers.push(id);
         s.testres.simca = null;
         s.testres.pca = null;
      }
   }

   // show modal dialog with contributions for selected object
   function showContributions(pos) {
      if (pos < 0) return;
      selected = pos;
      contributionIsShown = true;
   }

   // hide modal dialog with contributions
   function hideContributions() {
      contributionIsShown = false;
      selected = -1;
   }

   // callback to reset outliers
   function resetOutliers() {
      s.outliers = [];
      s.testres.simca = null;
      s.testres.pca = null;
   }

   // callback to undo outliers
   function undoOutliers() {
      s.outliers.pop();
      s.testres.simca = null;
      s.testres.pca = null;
   }

   // pre-rendering effect to avoid bad structure of the session object
   $effect.pre(() => {

      // if training set is set null - reset model and all related parameters
      if (s.testset === null) {
         s.outliers = [];
         s.testres.simca = null;
         s.testres.pca = null;
      }

      // if model object is set to null - reset dependent objects
      if (s.model === null) {
         s.outliers = [];
         s.simca = null;
         s.testres.simca = null;
         s.testres.pca = null;
         s.testset = null;
      }
   });

   // if model object is set to null - reset dependent objects
   $effect(() => {
      // if model object is set to null - reset dependent objects
      if (s.model === null) {
         s.outliers = [];
         s.testres.simca = null;
         s.testres.pca = null;
         s.testset = null;
      }
   });

   // if training set is set null - reset model and all related parameters
   $effect(() => {
      // if training set is set null - reset model and all related parameters
      if (s.testset === null) {
         s.outliers = [];
         s.testres.simca = null;
         s.testres.pca = null;
      }
   });


   // effects to track changes in eCrit and oCrit for recalculations of simca results
   $effect(() => {
      if (s.testres.pca && s.simca) {
         s.testres.simca = predict(s.testres.pca, untrack(() => s.simca.limType), s.simca.eCrit, s.simca.oCrit)
      }
   });


   // effect for triggering the session updates
   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      backupSession(sessionStr, s.model, s.testset, s.testres.simca, s.simca.ncomp);

      if (s.simca) {
         backupSession(sessionStr, s.model, s.testset, s.simca.ncomp, s.testres.simca);
      } else {
         backupSession(sessionStr, s.model, s.testset, s.simca, s.testres.simca);
      }
   });
</script>

{#if contributionIsShown}
   <Contributions
      dataset={s.testset}
      model={s.model}
      ncomp={s.simca.ncomp}
      outliers={s.outliers}
      {selected}
      closeModal={hideContributions}
   />
{/if}

<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="model-loader">
         <ModelLoader bind:model={s.model} bind:simca={s.simca} />
      </div>

      {#if s.model}
      <div class="data-loader">
         <DataLoader
            bind:dataset={s.testset}
            bind:resobj={s.testres.pca}
            outliers={s.outliers}
            {WASM}
            model={s.model}
            className="Class"
         />
      </div>
      {/if}

      {#if s.outliers && s.outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers outliers={s.outliers} dataset={s.testset} {resetOutliers} {undoOutliers} />
      </div>
      {/if}

      <div class="simca-settings">

         {#if s.model && s.testres.pca }
         <SIMCASettings
            model={s.model}
            className={s.className}
            bind:simca={s.simca}
         />
         {/if}

         {#if s.testres.simca }
         <Container label='Download'>
            <div class="buttons">
            <Button onclick={() => downloadResults(s.model, s.simca, s.testres, s.testset, "target", s.outliers)} text="Res"/>
            <Button onclick={() => downloadModel(s.model, s.simca)} text="Model" />
            </div>
         </Container>
         {/if}
      </div>

      {#if s.testres.simca}
      <div class="model-stats">
         <ModelTable model={s.model} simca={s.simca} />
      </div>
      <div class="result-stats">
         <ResultTable res={s.testres.simca} ncomp={s.simca.ncomp}/>
      </div>
      {/if}
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.testres.simca}
         <div class="help-text">
         <p>
            In this tab you can apply previously trained DD-SIMCA model to a dataset consisting of target class members (at least
            the app will assume this, you can of course provide any dataset). You can use it for testing performance
            of the model, or tune its parameters, if necessary.
         </p>
         {#if !s.model}
            <p><strong>Step 1</strong></p>
            <p>
               Load previously trained DD-SIMCA model from file.
            </p>
         {:else}
            {#if !s.testset}
            <p><strong>Step 2</strong></p>
            <p>
               Load testset set from CSV file. The requirements to the file are the same as for the
               training set. The testset may contain class labels for each row (you need to select this option in settings after selecting CSV file) but it is not necessary, as in any case the labels will be ignored and model will assume that all samples are from the target class.
            </p>
            {/if}
         {/if}

         </div>
      {:else}
         {#key s.testres.pca }
         <AppPlots {markOutlier} {showContributions} {...s} />
         {/key}
      {/if}
   </div>
</div>

<style>

   .app-container {
      font-family: Helvetica, Arial, sans-serif;
      font-size: 1rem;
      color: #404040;
      box-sizing: border-box;
      margin: 0 auto;
      padding: 0.35em 0.25em;
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-areas: "controls content";
      grid-template-rows: 100%;
      grid-template-columns: min(310px, 30%) 1fr;
   }

   .app-controls {
      box-sizing: border-box;
      padding: 0.25em 0.5em;
   }

   .app-content {
      box-sizing: border-box;
      padding: 0 1em 0 0.5em;
      padding: 0.25em 0.25em;
      height: 100%;
   }

   .app-controls > div {
      border-radius: 0.25rem;
   }

   .app-controls > .model-stats,
   .app-controls > .result-stats {
      background: #e8e8e8;
   }

   .app-controls > .model-stats,
   .app-controls > .result-stats {
      padding: 0.5rem 1rem;
      margin-top: 0.25rem;
      font-size: 0.85em;
   }


   .help-text {
      margin-top: 0.25rem;
   }

   .help-text > p {
      background: #fafafa;
      padding: 0em 1.5em 0.5em 1.5em;
      color: #606060;
      line-height: 1.5em;
      margin: 0 1em 0 0;
   }

   .help-text > p:first-child {
      border-top-left-radius: 0.25rem;
      border-top-right-radius: 0.25rem;
      padding-top: 1.5em;
   }

   .help-text > p:last-child {
      border-bottom-left-radius: 0.25rem;
      border-bottom-right-radius: 0.25rem;
      padding-bottom: 1.5em;
   }



   @media(min-width:1300px) {
      .app-container {
         grid-template-columns: min(340px, 30%) 1fr;
      }
   }

   @media(min-width:1100px) {
      .app-container {
         grid-template-columns: min(325px, 30%) 1fr;
      }
   }

</style>