import {Select, PlotTypeSelector, RangeDiscrete, Switch} from 'svelte-controls-basic';

export function getPlotAcceptanceControls(res) {

   const resIDs = Object.keys(res);
   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Result', props: {options: resIDs}},
      transType: { el: Select, hidden: false, default: 'no', label: 'Transform', props: {options: ['no', 'log']}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Show labels', props: {options: ['hide', 'show']}},
      colorBy: {el: Select, hidden: true, default: 'roles', label: 'Color by', props: {options: ['roles', 'classes']}},
   };

   return controls;
}

export function getPlotClassStatControls(resIDs) {

   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Show', props: {options: resIDs}},
      zoom: { el: Switch, hidden: false, default: false, label: 'Zoom', props: {options: ['off', 'on']}},
   };

   return controls;
}

export function getPlotDistanceControls(res) {

   const resIDs = Object.keys(res);
   const resobj = res[resIDs[0]];

   const controls = {
      ncomp: { el: RangeDiscrete, hidden: false, default: resobj.ncomp > 1 ? 2 : 1, label: '# PCs', props: {min: 1, max: resobj.ncomp}},
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Show', props: {options: resIDs}},
      transType: { el: Select, hidden: false, default: 'no', label: 'Transform', props: {options: ['no', 'log']}},
      limType: { el: Select, hidden: false, default: 'classic', label: 'Limits', props: {options: ['classic', 'robust']}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Show labels', props: {options: ['hide', 'show']}},
   };

   if (resobj.classNames && resobj.classNames.length > 1) {
      controls['colorBy'] = { el: Select, hidden: false, default: 'none', label: 'Color by', props: {options: ['none', 'groups']}}
   }
   return controls;
}


export function getPlotDistancesControls(res) {

   const resIDs = Object.keys(res);
   const options = resIDs.concat(['both', 'diff']);

   const controls = {
      resID: { el: Select, hidden: resIDs.length == 1, default: resIDs[0], label: 'Result', props: {options: options}},
      distType: { el: Select, hidden: false, default: 'h', label: 'Distance', props: {options: ['h', 'q', 'f']}},
      showLabels: { el: Switch, hidden: false, default: false, label: 'Show labels', props: {options: ['hide', 'show']}},
   };

   return controls;
}

export function getPlotDoFControls() {
   const controls = {
      dfID: { el: Select, hidden: false, default: 'both', label: 'DoF type', props: {options: ['both', 'Nh', 'Nq']}},
   };
   return controls;
}

