import { Vector } from 'mdatools/arrays';
import { mean, variance } from 'mdatools/stat';
import { pnorm } from 'mdatools/distributions';

import { reviver, replacer } from '../../shared/utils';

import { pcares_get, pcamodel_get } from '../../shared/wasm/dist/pca.js';
import { convertToIntervals } from '../../shared/utils.js';


/**
 * Compute SIMCA predictions for alternative dataset.
 *
 * @param {Object} pcares - JSON with predicted PCA results.
 * @param {Object} simca - JSON with SIMCA parameters.
 * @param {outliers} classes - array with outliers.
 *
 * @returns {Object} JSON with SIMCA parameters.
 */
export function predictAlt(pcares, limType, Nf, eCrit, oCrit) {

   const ncomp = pcares.ncomp;
   const nrows = pcares.nrows;

   const res = new Array(ncomp);
   const specificity = new Array(ncomp);
   const selectivity = new Array(ncomp);
   const components = new Array(ncomp)

   for (let a = 0; a < ncomp; a++) {
      const fa = pcares_get(pcares, (limType === 'robust' ? 'fdistr' : 'fdistc'), a + 1);
      const ra = predictA(false, fa, eCrit[a], oCrit[a]);


      let I = pcares.nrows;

      // get distances
      const fcrit = eCrit[a];

      // get DoF for full distance
      const k = Nf[a];

      // 	Order f' such that f1'≤ f2'≤.... ≤ fI''
      const fv = new Vector(fa);
      let fp = fv.sort();
      let ind = fv.sortind();

      // prepare array for roles
      const roles = Array.from({length: nrows}, () => ("alien"));;

      // Step 2. If Disc <0
      let Disc = -1;
      let n = 0;
      let m = 0;
      let d = 0;
      let M1 = 0;

      while (Disc < 0) {
         if (n > 0) {
            roles[ind.v[I - 1] - 1] = 'external';
            I = I - 1;
            ind = ind.slice(1, I);
            fp = fp.slice(1, I);
         }
         m = mean(fp);
         d = variance(fp, true);
         M1 = d / (m * m);
         Disc = 4 - 2 * k * M1;
         n = n + 1;
      }

      // Step 3. Calculate  x  by Eq. (18)
      const x = (2 + Math.sqrt(Disc)) / M1;

      // Calculate  s and  f'0 by Eq. (19)
      const s = (x - k);
      const f0 = m / x;


      // Calculate: z, h, r, p, Mz, Sz by Eq. (20)
      const z = fcrit / f0;
      const h = 1 - 2 * (k + s) * (k + 3 * s) / (3 * (k + 2 * s)**2);
      const r = (h - 1) * (1 - 3 * h);
      const p = (k + 2 * s) / (k + s)**2;
      const Mz = 1 + h * p * (h - 1 - 0.5 * (2 - h) * r * p);
      const Sz = h * Math.sqrt(2 * p) * (1 + 0.5 * r * p);

      // Step 3.	If α is given then calculate β by Eq. (21)
      const beta = pnorm((Math.pow(z / (k + s), h) - Mz) / Sz);
      res[a] = ra;
      res[a].beta = beta;
      res[a].s = s;
      res[a].k = k;
      res[a].Mz = Mz;
      res[a].Sz = Sz;
      res[a].m = m;
      res[a].f0 = f0;
      res[a].hz = h;
      res[a].anum = {'aliens': I, 'external': nrows - I};
      res[a].a = roles;

      selectivity[a] = 1 - beta;
      specificity[a] = ra.TN / (ra.TN + ra.FP);
      components[a] = a + 1;
   }

   return {
      class: ['simcares'],
      classes: pcares.classes,
      classNames: pcares.classNames,
      target: false,
      v: res,
      nrows: pcares.nrows,
      stat: {specificity, selectivity, components}
   }
}



/**
 * Compute SIMCA predictions for a particular component
 */
export function predictA(target, f, eCrit, oCrit) {

   // get object distances
   const nrows = f.length;

   // prepare array for object categories
   const c = new Array(nrows);
   const d = new Array(nrows);
   const cnum = {'regular': 0, 'extreme': 0, 'outliers': 0};
   const dnum = {'in': 0, 'out': 0};

   let TP = 0, FP = 0, TN = 0, FN = 0;
   for (let k = 0; k < nrows; k++) {
      const fk = f[k];
      if (fk < eCrit) {
         c[k] = 'regular';
         d[k] = 'in';
         cnum.regular += 1;
         dnum.in += 1;
      } else if (fk > oCrit) {
         c[k] = 'outlier';
         d[k] = 'out';
         cnum.outliers += 1;
         dnum.out += 1;
      } else {
         c[k] = 'extreme';
         d[k] = 'out';
         cnum.extreme += 1;
         dnum.out += 1;
      }

      TP = TP + (d[k] === 'in' && target);
      FN = FN + (d[k] !== 'in' && target);
      TN = TN + (d[k] !== 'in' && !target);
      FP = FP + (d[k] === 'in' && !target);
   }

   return {f, c, d, cnum, dnum, TP, TN, FN, FP};
}

/**
 * Compute SIMCA predictions.
 *
 * @param {Object} pcares - JSON with predicted PCA results.
 * @param {Object} simca - JSON with SIMCA parameters.
 * @param {outliers} classes - array with outliers.
 *
 * @returns {Object} JSON with SIMCA parameters.
 */
export function predict(pcares, limType, eCrit, oCrit) {

   const ncomp = pcares.ncomp;

   const res = new Array(ncomp);
   const sensitivity = new Array(ncomp);
   const components = new Array(ncomp)
   for (let a = 0; a < ncomp; a++) {
      const fa = pcares_get(pcares, (limType === 'robust' ? 'fdistr' : 'fdistc'), a + 1);
      const ra = predictA(true, fa, eCrit[a], oCrit[a]);
      sensitivity[a] = ra.TP / (ra.TP + ra.FN);
      components[a] = a + 1;
      res[a] = ra;
   }

   return {
      class: ['simcares'],
      classes: pcares.classes,
      target: true,
      v: res,
      nrows: pcares.nrows,
      stat: {sensitivity, components}
   }
}


// function for downloading of PV-set
export function downloadResults(model, simca, res, dataset, name, outliers) {

   if (!outliers || !Array.isArray(outliers)) {
      outliers = [];
   }

   let exclvars = model.exclvars;
   if (!exclvars || !Array.isArray(exclvars)) {
      exclvars = [];
   }

   const nAll = dataset.values.nrows;
   const nOut = outliers.length;
   const n = nAll - nOut;

   const ncomp = simca.ncomp;
   const q = pcares_get(res.pca, 'qdist', ncomp);
   const h = pcares_get(res.pca, 'hdist', ncomp);

   const lt = simca.limType === 'classic' ? 'c' : 'r';
   const f = pcares_get(res.pca, 'fdist' + lt, ncomp);
   const Nh = pcamodel_get(model, lt + 'Nh')[ncomp - 1];
   const Nq = pcamodel_get(model, lt + 'Nq')[ncomp - 1];
   const h0 = pcamodel_get(model, lt + 'h0')[ncomp - 1];
   const q0 = pcamodel_get(model, lt + 'q0')[ncomp - 1];
   const Nf = Nh + Nq;

   const v = res.simca.v[ncomp - 1];
   const sep = dataset.sep;

   const hasClasses = res.simca.classes && res.simca.classes.length === n;
   const filler = hasClasses ? ['', '', '', '', ''] : ['', '', '', ''];
   let out = '';
   out += ['Reference classes:',  hasClasses ? 'provided' : 'not provided', ...filler].join(sep) + '\n';
   out += ['Number of components:', ncomp, ...filler].join(sep) + '\n';
   out += ['Alpha:', (simca.alpha/100).toFixed(3), ...filler].join(sep) + '\n';
   out += ['Limits estimator:', simca.limType, ...filler].join(sep) + '\n';
   out += '\n';

   out += ['Nh:', Nh, ...filler].join(sep) + '\n';
   out += ['Nq:', Nq, ...filler].join(sep) + '\n';
   out += ['Nf:', Nf, ...filler].join(sep) + '\n';
   out += '\n';

   out += ['F crit (extremes):', simca.eCrit[ncomp - 1], ...filler].join(sep) + '\n';
   out += ['F crit (outliers):', simca.oCrit[ncomp - 1], ...filler].join(sep) + '\n';
   out += '\n';

   if (exclvars.length > 0) {
      out += ['Number of excluded varibles:', exclvars.length, ...filler].join(sep) + '\n';
      out += ['Indices of excluded varibles:', convertToIntervals(exclvars), ...filler].join(sep) + '\n';
      out += '\n';
   }

   if (v.beta !== null && v.beta !== undefined) {
      // there can be no removed outliers in this case
      out += ['Objects treated as from non-target class', '', ...filler].join(sep) + '\n';
      out += ['Objects:', nAll, ...filler].join(sep) + '\n';
      out += ['In:', v.dnum.in, ...filler].join(sep) + '\n';
      out += ['Out:', v.dnum.out, ...filler].join(sep) + '\n';
      out += ['Aliens:', v.anum.aliens, ...filler].join(sep) + '\n';
      out += ['External:', v.anum.external, ...filler].join(sep) + '\n';
      out += ['Specificity:', res.simca.stat.specificity ? res.simca.stat.specificity[ncomp - 1] : '-', ...filler].join(sep) + '\n';
      out += ['Selectivity:', res.simca.stat.selectivity ? res.simca.stat.selectivity[ncomp - 1] : '-', ...filler].join(sep) + '\n';
      out += ['Beta:', v.beta, ...filler].join(sep) + '\n';
      out += ['Non-centrality, s:', v.s, ...filler].join(sep) + '\n';
      out += ['Scaling, f0:', v.f0, ...filler].join(sep) + '\n';
      out += '\n';

      out += ['', 'object', 'class', 'role', 'decision', 'h/h0', 'q/q0', 'f'].join(sep) + '\n';

      // in this case no outliers are expected
      for (let i = 0; i < nAll; i++) {
         out += [
            '',
            dataset.objlabels[i],
            res.simca.classes[i],
            v.a[i],
            v.d[i],
            h[i] / h0,
            q[i] / q0,
            f[i]
         ].join(sep) + '\n';
      }

   } else {
      // there can be no removed outliers in this case
      out += ['Objects treated as from target class', '', ...filler].join(sep) + '\n';
      out += ['Objects:', nAll, ...filler].join(sep) + '\n';
      out += ['Removed:', nOut, ...filler].join(sep) + '\n';
      out += ['In:', v.dnum.in, ...filler].join(sep) + '\n';
      out += ['Out:', v.dnum.out, ...filler].join(sep) + '\n';
      out += ['Regular:', v.cnum.regular, ...filler].join(sep) + '\n';
      out += ['Extreme:', v.cnum.extreme, ...filler].join(sep) + '\n';
      out += ['Outliers:', v.cnum.outliers, ...filler].join(sep) + '\n';
      out += ['Sensitivity:', res.simca.stat.sensitivity ? res.simca.stat.sensitivity[ncomp - 1] : '-', ...filler].join(sep) + '\n';
      out += '\n';

      out += ['', 'object', 'class', 'role', 'decision', 'h/h0', 'q/q0', 'f'].join(sep) + '\n';

      let j = 0;
      for (let i = 0; i < nAll; i++) {

         if (nOut > 0 && outliers.includes(i)) {
            out += [
               '',
               dataset.objlabels[i],
               '-',
               'removed',
               '-',
               '-',
               '-',
               '-'
            ].join(sep) + '\n';
            continue;
         }

         out += [
            '',
            dataset.objlabels[j],
            res.pca.classes[j],
            v.c[j],
            v.d[j],
            h[j] / h0,
            q[j] / q0,
            f[j]
         ].join(sep) + '\n';
         j = j + 1;
      }
   }
   out += '\n';



   if (dataset.dec !== '.') {
      out = out.replace(/\./g, dataset.dec);
   }

   out = ['Target class:', simca.className, ...filler].join(sep) + '\n'  + out;
   out = ['Data filename:', dataset.fileName, ...filler].join(sep) + '\n' + out;

   let hdr = ['DD-SIMCA results (' + name + ')', 'https://mda.tools/ddsimca/', ...filler].join(sep) + '\n\n';

   const blob = new Blob([hdr + out], { type: 'text/csv' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', `ddsimca-results-${name}.csv`);
   a.click()
}


// function for downloading of PV-set
export function downloadModel(model, simca) {

   const blob = new Blob([JSON.stringify({pca: model, simca}, replacer)], { type: 'application/json' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', 'ddsimca-model.json');
   a.click()
}
