<script>
   /**************************************************************/
   /* SIMCASettings                                              */
   /* -------------------------------------                      */
   /* shows panel with SIMCA model settings.                     */
   /**************************************************************/

   import { qchisq } from 'mdatools/distributions';
   import { Select, Container, Range } from 'svelte-controls-basic';
   import { genhash } from '../../../shared/utils.js';
   import { pcamodel_get } from '../../../shared/wasm/dist/pca.js';

   let {
      model,
      className,
      isAlt = false,
      simca = $bindable(),
   } = $props();

   let oldAlpha = $state();
   let oldGamma = $state();

   // initialize SIMCA object with default values
   if (!simca && model) {

      const alpha = 5;
      const gamma = 1;
      const limType = 'classic';

      const Nh = pcamodel_get(model, 'cNh');
      const Nq = pcamodel_get(model, 'cNq');
      const Nf = Nh.map((v, i) => v + Nq[i]);

      const ep = 1 - alpha / 100;
      const op = Math.pow(1 - gamma / 100, 1 / model.nrows);
      const eCrit = Nf.map(v => qchisq(ep, v));
      const oCrit = Nf.map(v => qchisq(op, v));

      oldAlpha = alpha;
      oldGamma = gamma;

      simca = {
         ncomp: 1,
         alpha,
         gamma,
         limType,
         Nf,
         eCrit,
         oCrit,
         className,
         hash: genhash()
      };
   }

   // possible choices for alpha and gamma
   const alphaOptions = [10, 5, 1, 0.1];
   const gammaOptions = [5, 1, 0.1, 0.01];


   // if alpha or gamma changed - recompute the critical values for extremes
   $effect(() => {

      if (simca.alpha && oldAlpha !== simca.alpha) {
         const p = 1 - simca.alpha / 100;
         simca.eCrit = simca.Nf.map(v => qchisq(p, v));
         oldAlpha = simca.alpha;
      }

      if (simca.gamma && oldGamma !== simca.gamma) {
         const p = Math.pow(1 - simca.gamma / 100, 1 / model.nrows);
         simca.oCrit = simca.Nf.map(v => qchisq(p, v));
         oldGamma = simca.gamma;
      }

   });

   // if limType has changed - recompute Nf and all limits
   $effect(() => {
      const lt = simca.limType.charAt(0);
      const Nh = pcamodel_get(model, lt + 'Nh');
      const Nq = pcamodel_get(model, lt + 'Nq');
      const Nf = Nh.map((v, i) => v + Nq[i]);
      const pe = 1 - simca.alpha / 100;
      const po = Math.pow(1 - simca.gamma / 100, 1 / model.nrows);
      const eCrit = Nf.map(v => qchisq(pe, v));
      const oCrit = Nf.map(v => qchisq(po, v));
      simca.Nf = Nf;
      simca.eCrit = eCrit;
      simca.oCrit = oCrit;
   });

</script>

{#if simca}
<Container name="target-class" label="">
   <p class="title">Target class: {simca.className}</p>
</Container>

<Container name="ncomp" label="# of PCs">
   <Range bind:value={simca.ncomp} min={1} max={model.ncomp} step={1} decNum={0} />
</Container>

<Container name="alpha" label='Alpha, %:'>
   <Select bind:value={simca.alpha} options={alphaOptions} />
</Container>

{#if !isAlt}
<Container name="gamma" label='Gamma, %:'>
   <Select bind:value={simca.gamma} options={gammaOptions} />
</Container>
{/if}

<Container name="limits" label='Limits:'>
   <Select bind:value={simca.limType} options={['classic', 'robust']} />
</Container>
{/if}
