<script>
   /**************************************************************/
   /* ResultsTable                                               */
   /* -------------------------                                  */
   /* shows table with SIMCA results for training and PV-sets    */
   /**************************************************************/

   // main properties of the whole app
   let { calres, pvres, ncomp } = $props();
   let nrows = $derived(calres ? calres.v[0].c.length : 0);
</script>


{#if calres && pvres}
<table>
   <thead>
   <tr>
      <th></th>
      <th>Cal</th>
      <th>PV</th>
   </tr>
   </thead>
   <tbody>
   <tr>
      <td class="label">Objects</td>
      <td>{nrows}</td>
      <td>{nrows}</td>
   </tr>
   <tr>
      <td class="label">Extremes</td>
      <td>{calres.v[ncomp - 1].cnum.extreme}</td>
      <td>{pvres.v[ncomp - 1].cnum.extreme}</td>
   </tr>
   <tr>
      <td class="label">Outliers</td>
      <td>{calres.v[ncomp - 1].cnum.outliers}</td>
      <td>{pvres.v[ncomp - 1].cnum.outliers}</td>
   </tr>
   <tr>
      <td class="label">Sensitivity</td>
      <td>{calres.stat.sensitivity[ncomp - 1].toFixed(3)}</td>
      <td>{pvres.stat.sensitivity[ncomp - 1].toFixed(3)}</td>
   </tr>
   </tbody>
</table>
{/if}

