<script>
   /*************************************************************/
   /* Root component for the application                        */
   /*************************************************************/

   import { date2str } from '../../shared/utils';

   import TrainAppContainer from './tab_train/AppContainer.svelte';
   import TargetAppContainer from './tab_target/AppContainer.svelte';
   import AltAppContainer from './tab_new/AppContainer.svelte';
   import App from '../../shared/components/MainApp.svelte';
   import WASM from '../../shared/wasm/dist/pca_wasm.js';

   /* callback for reseting the app via session */
   function resetSession(s) {
      s.train.dataset = null;
      s.target.model = null;
      s.new.model = null;
      s.tab = 0;
   }

   /* callback to check validity of JSON with session */
   function checkSession(s) {
      const keys1 = Object.keys(s);
      if (!['date', 'tab', 'train', 'target', 'new'].every(v => keys1.includes(v))) return false;

      /*!!!!change because in this case different tabs have different fields!!!*/
      const keys2 = Object.keys(s.train);
      if (!['model', 'simca', 'calset', 'pvset', 'calres', 'pvres', 'outliers'].every(v => keys2.includes(v))) return false;

      const keys3 = Object.keys(s.target);
      if (!['model', 'simca', 'testset', 'testres', 'outliers'].every(v => keys3.includes(v))) return false;

      const keys4 = Object.keys(s.new);
      if (!['model', 'simca', 'altset', 'altres', 'outliers'].every(v => keys4.includes(v))) return false;

      return true;
   }

   /* callback to initialize new session */
   function initSession() {
      return {
         date: date2str(new Date()),
         tab: 0,
         train: {
            model : 0,
            simca: 0,
            calset: 0,
            pvset: 0,
            calres: {pca: 0, simca: 0},
            pvres: {pca: 0, simca: 0},
            outliers: [],
         },
         target: {
            model : 0,
            simca: 0,
            testset: 0,
            testres: {pca: 0, simca: 0},
            outliers: []
         },
         new: {
            model : 0,
            simca: 0,
            altset: 0,
            altres: {pca: 0, simca: 0},
            outliers: []
         }
      }
   }

   // app containers
   const tabs = [
      {name: 'Train', label: 'train', component: TrainAppContainer},
      {name: 'Predict: Target', label: 'target', component: TargetAppContainer},
      {name: 'Predict: New', label: 'new', component: AltAppContainer},
   ]
</script>

<App
   appID="ddsimca"
   appTitle="Data Driven SIMCA"
   appShortTitle="DD-SIMCA"
   appSlogan="One class classifier"
   {WASM}
   {tabs}
   {initSession}
   {checkSession}
   {resetSession}
>

   {#snippet footer()}
      <p>Current release: <span>2.1.8</span>
      Feedback: <a class="paper-link" href="mailto:help@mda.tools?subject=DDSIMCA app">help@mda.tools</a>
      Tutorial: <a class="paper-link" href="Oregano.zip" download>dataset</a>
      | <a href="https://www.youtube.com/playlist?list=PLep7KM9cEyFmoOR8sWDt7vJdnOCHvPEo2">video lessons</a>
      </p>
   {/snippet}

   {#snippet info()}
   <h2>Data Driven SIMCA</h2>
   <p>Authors: <em>Sergey Kucheryavskiy, Oxana Rodionova, Alexey Pomerantsev</em></p>
   <p>
      Interactive web-application for creating and using DD-SIMCA one class classifier
      directly in your browser. The app does not send any information to a server,
      all calculations are done locally on your device.
   </p>
   <p>Check our open access <a href="https://doi.org/10.1002/cem.3556">tutorial paper</a> and YouTube <a href="https://www.youtube.com/playlist?list=PLep7KM9cEyFmoOR8sWDt7vJdnOCHvPEo2">video-lessons</a>
   <p>
      Related theory:
      <a href="https://doi.org/10.1002/cem.2506">10.1002/cem.2506</a>,
      <a href="https://doi.org/10.1002/cem.2610">10.1002/cem.2610</a>,
      <a href="https://doi.org/10.1002/cem.3250">10.1002/cem.3250</a>,
      <a href="https://doi.org/10.1016/j.aca.2024.343352">10.1016/j.aca.2024.343352</a>.
   </p>
   <h3>v. 2.1.8</h3>
   <p class="release-info">released 16/04/2025</p>
   <ul>
      <li>App now remembers the plot settings when you switch between plots.</li>
      <li>Introducing sessons — app states, including model, results, datasets, and outliers
         (in all tabs). When you close tab or app window or click on reboot button, the current
         session is saved to browser's local storage and then is restored automatically. You can
         also download session as a file and load it from file.
      </li>
      <li>Added undo option for outliers (if more than one is removed).</li>
      <li>More robust CSV converter which filters out empty rows created by some versions of MS Excel.</li>
      <li>Results for alternative set also contains parameters <em>s</em> and <em>f'<sub>0</sub></em> for LoD estimation.</li>
      <li>Many small improvements to plots and control elements.</li>
   </ul>
   {/snippet}
</App>

