<script>
   /****************************************************************/
   /* PlotClassStat                                                */
   /* --------------------                                         */
   /* shows plot with classification statistics vs number of PCs   */
   /****************************************************************/

   import { range, mrange } from 'mdatools/stat';
   import { Segments, Legend, Rectangles } from 'svelte-plots-basic/2d';
   import { LINE_COLORS, FACE_COLORS } from './Defaults.js';
   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      props,                         // JSON with main properties (model, results, dataset)
   } = $props()

   // file name for plot graphic file
   let fileName = $derived('plot-' + params.statName);

   // get the properties needed for the plot (note we use classres here not res)
   let res = $derived(props && props.classres ? props.classres : null);
   let ncomp = $derived(props && props.ncomp ? props.ncomp : null)
   let resIDs = $derived(params.resID !== 'both' ? [params.resID] : Object.keys(res));
   let legendPosition = $derived(params.legendPosition ? params.legendPosition : 'bottomright');

   // compute max number of components and collect legend items
   let legendItems = $derived(resIDs ? resIDs.map(v => ({
      id: v,
      label: v,
      point: {
         marker: 1,
         lineColor: LINE_COLORS[v],
         faceColor: FACE_COLORS[v],
         markerSize: 0.85,
         lineWidth: 1.5
      },
      line: {
         lineType: 1,
         lineColor: LINE_COLORS[v],
      }
   })) : null);

   // compute ci
   let ci = $derived(params.ci && Array.isArray(params.ci) && params.ci.length === 2 ? params.ci : null);


   // compute limits
   const nc = $derived(res ? res[resIDs[0]].stat.components.length : 1);
   const dX = $derived(nc > 10 ? nc * 0.05 : 0.5)
   const xMin = $derived(nc > 1 ? 1 : 0);
   const xMax = $derived(nc > 1 ? nc : 2);
   const limX = $derived([xMin - dX, xMax + dX]);


   let limY = $derived.by(() => {
      if (!params.zoom) return [0, 1.1];
      if (!resIDs || resIDs.length < 1) return [0, 1];
      const v = resIDs.map(id => range(res[id].stat[params.statName]));
      return mrange(v.flat(), 0.2);
   });
</script>

{#if res }

<Axes title={params.title} xLabel='Components' yLabel={params.title} {limX} {limY} {fileName} whole={true}>

   <!-- vertical line with selected component -->
   {#if ncomp}
   <Segments xStart={[ncomp]} xEnd={[ncomp]} yStart={[limY[0]]} yEnd={[limY[1]]} lineColor='#a0a0a0' />
   {/if}

   <!-- confidence interval -->
   {#if ci}
   {@const dX = limX[1] - limX[0]}
   <Rectangles left={[limX[0]]} top={[ci[0] + ci[1]]} height={[2 * ci[1]]} width={[dX]} lineColor='#transparent' faceColor='#00000010'/>
   <Segments xStart={[limX[0]]} xEnd={[dX]} yStart={[ci[0]]} yEnd={[ci[0]]} lineColor='#a0a0a0' lineType={2}/>
   {/if}

   <!-- data points with statistics -->
   {#each resIDs as id, i}
      <PlotSeries
         xValues={res[id].stat.components}
         yValues={res[id].stat[params.statName]}
         plotType='b'
         showLabels={false}
         faceColor={FACE_COLORS[id]}
         mainColor={LINE_COLORS[id]}
         lineWidth={1.5}
      />
   {/each}

   <!-- points for selected component -->
   {#if ncomp}
   {#each resIDs as id}
      <PlotSeries
         xValues={[res[id].stat.components[ncomp - 1]]}
         yValues={[res[id].stat[params.statName][ncomp - 1]]}
         plotType='b'
         showLabels={false}
         faceColor={LINE_COLORS[id]}
         mainColor={'#a0a0a0'}
         markerSize={1.2}
         lineWidth={1}
      />
   {/each}
   {/if}


   {#if legendItems && legendItems.length > 0}
   <Legend items={legendItems} position={legendPosition} />
   {/if}
</Axes>
{/if}